import React from 'react';
import AdminLayout from './AdminLayout';
import Menu from './Menu';

import '../../profile/styles/profile-view.scss';

class AdminView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentView: 'manageLocations'
    };
  }

  handleProductSelect = e => this.setState({ selectedProduct: e.target.value });

  render() {
    return (
      <AdminLayout>
        <div className=" admin profile-view">
          <aside className="profile-view__sidebar">
            <Menu {...this.state} {...this.props} />
          </aside>
          <section className="profile-view__content">{this.props.children}</section>
        </div>
      </AdminLayout>
    );
  }
}

export default AdminView;
